import React from 'react';
import styled from 'styled-components';
import Data from '../data/Data'


const HomeSection = styled.section`
    width: '100%';
    display: flex;
    flex-direction: column;
    align-items: center;
`



const Home = () => {
    return (
        <>
            <HomeSection>
                <Data  />
            </HomeSection>
        </>   
    );
}
 
export default Home;