import React from 'react'
import { useSpring, animated } from 'react-spring'
import Home from '../pages/Home'

import './styles.css' // // Icons made by Freepik from www.flaticon.com

const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2]
const trans1 = (x, y) => `translate3d(${x / 10}px,${y / 8 - 10}px,0)`
const trans2 = (x, y) => `translate3d(${x / 8 + 10}px,${y / 8 - 230}px,0)`

function Card() {
  const [props, set] = useSpring(() => ({ xy: [0, 0], config: { mass: 10, tension: 500, friction: 200 } }))
  return (
    <>
      <div className="container" onMouseMove={({ clientX: x, clientY: y }) => set({ xy: calc(x, y) })}>
        
        <animated.div className="card1" style={{ transform: props.xy.interpolate(trans1) }} >
          <Home />
        </animated.div>
        <animated.div className="card2" style={{ transform: props.xy.interpolate(trans2) }} />

      </div>
    </>
  )
}

export default Card;