import React from 'react';
import styled from 'styled-components';

const Navbar = styled.nav`
    width: 100%;
    height:  60px;
    /* background: #ffaf; */
    color: blue;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;

`
const Logo = styled.a`
    font-size: 40px;
    font-style: italic;
    color: black;
`

const Header = () => {
    return ( 
        <Navbar>
           <Logo> El Clima </Logo>
        </Navbar>
    );
}
 
export default Header;
