import React, { useState } from 'react';
import styled from 'styled-components';
import snow from '../images/snow.png'
import drizzle from '../images/drizzle.png'
import rain from '../images/rain.png'
import thunderstorm from '../images/thunderstorm.png'
import clear from '../images/clear.png'
import clouds from '../images/clouds.png'
import Card from '../components/CardSpring'

const api = {
    key: "e5950b5009f511b7b41799e3d7eea5fb",
    base: "https://api.openweathermap.org/data/2.5/",
    lang: "sp"
}

const backgroundWeather = [
  {
      img: snow,
      label: 'Nieve',
      main: 'Snow'
  },
  {
      img: drizzle,
      label: 'Llovizna',
      main: 'Drizzle'
  },
  {
      img: rain,
      label: 'Lluvioso',
      main: 'Rain'
  },
  {
      img: clear,
      label: 'Despejado',
      main: 'Clear'
  },
  {
      img: clouds,
      label: 'Nublado',
      main: 'Clouds'
  },
  {
      img: thunderstorm,
      label: 'Tormenta eléctrica',
      main: 'Thunderstorm'
  },
  
]



const Button = styled.button`
    color: palevioletred;
    font-size: 1em;
    margin: 5px;
    padding: 0.25em .25em;
    border: 2px solid palevioletred;
    border-radius: 10px;
    background-color: transparent;
`

const Input = styled.input`
    border: none;
    background-color: transparent;
    all: unset;
    color: black;
    padding-left: 20px;
    font-size: 20px;
    

`

const Browser = styled.div`
    width: 100%; 
    border: 2px solid palevioletred;
    border-radius: 20px;
    display: 'flex';
    justify-content: space-evenly;
    margin-bottom: 50px;

`

const Text = styled.p`
  margin: 50px 0 0 0;
  text-align: center;
`

const Data = () => {
    
    const [weather, setWeather] = useState({});
    const [query, setQuery] = useState([])

    const search = () => {
        fetch(`${api.base}weather?q=${query}&units=metric&APPID=db84cf2e4a85c1eab4fc8733b038c0fa`)
        .then(res => res.json())
        .then(result => {
            setWeather(result);
            setQuery('');
          });
          
    }

  const dateBuilder = (d) => {
    let months = ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"];
    let days = ["Domingo", "Lunes", "Martes", "Miercoles", "Jueves", "Viernes", "Sábado"];

    let day = days[d.getDay()];
    let date = d.getDate();
    let month = months[d.getMonth()];
    let year = d.getFullYear();

    return `${day} ${date} ${month} ${year}`
  }

  return (
    <div className={(typeof weather.main != "undefined") ? ((weather.main.temp > 16) ? 'app warm' : 'app') : 'app'}>
      <main>
        <Text>Ingrese el país o ciudad</Text>
        <Browser className="search-box">
          <Input 
            type="text"
            placeholder="Example: misiones, arg"
            onChange={e => setQuery(e.target.value)}
            value={query}
          >
          </Input>
            <Button onClick={search}> Buscar </Button>
        </Browser>
        {(typeof weather.main != "undefined") ? (
        <div>
          <Card weather={weather} dateBuilder={dateBuilder} backgroundWeather={backgroundWeather}/>
        </div>
        ) : ('')}
      </main>
      {/* {imgClima} */}
    </div>
  );
}
 
export default Data;