import React from 'react'
import { useSpring, animated } from 'react-spring'
import { Flip, Bounce} from 'react-reveal/';
import styled from 'styled-components';
import './stylesCardSpring.css'

const calc = (x, y) => [-(y - window.innerHeight / 2) / 20, (x - window.innerWidth / 2) / 20, 1.1]
const trans = (x, y, s) => `perspective(600px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`





function Card({weather, dateBuilder, backgroundWeather}) {
  const [props, set] = useSpring(() => ({ xys: [0, 0, 1], config: { mass: 5, tension: 350, friction: 40 } }))

const DivContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`

const DivImagen = styled.div`
    display: flex;
    align-items: center;
`

const Text = styled.p`
    font-size: 15px;
    font-weight: 700;
    margin-bottom: 0;
    padding-left: 10px;
`

  const imgClima = backgroundWeather.map((img, index) => {
    if( weather.weather[0].main === img.main){
        return (
            <Flip left>
                <DivContainer key={index}>
                    <DivImagen>
                        <Text>{img.label}</Text>
                        <img src={img.img} alt="img clima"/>
                    </DivImagen>
                </DivContainer>
            </Flip> 
        )
    } 
    
})

  return (
      <Bounce>  
            <animated.div
            className="card"
            onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
            onMouseLeave={() => set({ xys: [0, 0, 1] })}
            style={{ transform: props.xys.interpolate(trans) }}
            >
            <div>

                < Bounce><Text>{dateBuilder(new Date())}</Text></Bounce>
                < Bounce><Text>{weather.name}, {weather.sys.country}</Text></Bounce>
                < Bounce><Text>Temp. {Math.round(weather.main.temp)}°c</Text></Bounce>
            </div>
                {imgClima}

            </animated.div>
        </Bounce>
  )
}

export default Card;